* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.wrapper-content {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
}

.pic {
  display: block;
  font-size: 0;
  width: 100%;
}

.pic-map {
  margin-top: 30px;
}

.part {
  position: relative;
  z-index: 1;
  text-align: center;
}

.part-section {
  width: 1200px;
  margin: 0 auto;
}

.part-partner {
  padding: 80px 0;
}

.part-customer {
  padding: 30px 0 0 0;
}

.part-feature {
  padding: 80px 0;
}

.part-contact {
  background: #353637;
  color: #fff;
  padding: 80px 0 40px 0;
}

.part-contact .part-title-big {
  color: #fff;
}

.part-contactus {
  height: 900px;
  background-image: url("../img/pic-banner-contact.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
}

.part-contactus .part-title-big {
  padding-top: 20px;
}

.part-legal {
  font-size: 16px;
  padding: 40px 0;
  border-top: 1px solid #C2C2C2;
  background: #353637;
  color: #fff;
}

.part-title {
  margin-bottom: 40px;
}

.part-title-big {
  font-size: 36px;
  color: #333;
  letter-spacing: 1px;
  padding: 6px;
}

.partner-list {
  font-size: 0;
}

.partner-item {
  width: 33.3333%;
  display: inline-block;
  vertical-align: middle;
  padding: 10px 20px 10px 20px;
  margin-bottom: 20px;
}

.partner-item-title {
  font-size: 24px;
  margin-bottom: 30px;
}

.customer {
  padding: 40px 20px;
  background: #fff;
}

.customer-avatar {
  width: 100px;
  height: 100px;
  border-radius: 100%;
  margin: 0 auto 20px;
}

.customer-name {
  font-size: 24px;
  margin-bottom: 20px;
}

.customer-desc {
  height: 180px;
  overflow: scroll;
  padding: 20px 10px;
  font-size: 18px;
  line-height: 1.68;
  overflow: hidden;
}

.feature-list {
  font-size: 0;
}

.feature-item {
  width: 33.333%;
  display: inline-block;
  vertical-align: top;
  text-align: left;
  margin-bottom: 40px;
}

.feature-title {
  font-size: 24px;
  color: #3bbda8;
  margin-bottom: 16px;
}

.feature-desc {
  font-size: 18px;
}

.contact-content {
  margin-bottom: 60px;
}

.contact-desc {
  font-size: 18px;
  line-height: 1.68;
}

.contact-desc a {
  color: #3bbda8;
}

.contactus-content {
  background: #fff;
  border-radius: 4px;
  width: 600px;
  padding: 20px;
  position: absolute;
  right: 6%;
  bottom: 10%;
  font-weight: bold;
}

.contactus-list {
  padding-top: 40px;
}

.contactus-item {
  font-size: 0;
  margin-bottom: 30px;
}

.contactus-side {
  display: inline-block;
  vertical-align: middle;
  font-size: 24px;
}

.contactus-side a {
  display: inline-block;
  color: #3bbda8;
  text-decoration: none;
}

.contactus-left {
  width: 30%;
  text-align: right;
  padding-right: 4%;
}

.contactus-right {
  width: 70%;
  text-align: left;
  padding-left: 3%;
  font-size: 22px;
}

.nav {
  width: 100%;
  padding: 14px;
  background: rgba(0, 0, 0, 0.7);
  position: fixed;
  z-index: 9;
  left: 0;
  top: 0;
}

.nav .part {
  text-align: right;
}

.nav .part a {
  color: #3bbda8;
  font-size: 18px;
}

@media (max-width: 1200px) {
  .part {
    width: 1200px;
  }
}
